
@import "~normalize-scss/sass/normalize";
@import './variable.scss';

// @import './header.scss';
// @import './footer.scss';

html{
    font-size: 10px;
    
}
body{
    background-color:$Light_grayish_blue;
    @include font();
    
 
}
.container{
    max-width: 128rem;
    margin: 5rem auto;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
   // grid-auto-rows: 100px;
    grid-gap: 2rem;
   
    grid-auto-rows: minmax(100px, auto);
}


.blue-block{
    grid-column: 1 / 3;
    grid-row: 1 / 1;
    background: $violet;  
    border-radius: .8rem;
    &__text{
        color: #c49ffc;
    }
}
.grey-block{
    grid-column: 3 / 3;
    grid-row: 1 / 1;
    background: $dark_blue;
    border-radius: .8rem;  
}
.whaite-block{
    grid-column: 4 / 4;
    grid-row: 1 / 3;
    background: $Light_gray;
    border-radius: .8rem; 
    background: white;
    &__autor{
        span{
            color: $dark_blue;
            min-width: 100px;
        }
    }
    &__title{
        color: $dark_blue !important;
    }
    &__text{
       
    }
   
}
.whaite-short-block{
    grid-column: 1 / 2;
    grid-row: 2 / 2;
    background: $Light_gray;
    border-radius: .8rem;
    background: white; 
    &__autor{
        span{
            color: $dark_blue;
        }
    }
    &__title{
        color: $dark_blue !important;
    }
    &__text{
       
    }
}
.grey-long-block{
    grid-column: 2 / 4;
    grid-row: 2 / 2;
    background: $dark_blackish_blue;
    border-radius: .8rem;
    &__autor{
        color:white;
    }
}
.autor{
    display: flex;
    margin-left: 2rem;
    margin-top: 1.5rem;
    align-items: center;
    img{
        display: inline-block;
        border-radius: 100%;
        align-self: flex-end;
        
    }
    &_family{

    display: inline-flex;
    flex-direction: column;
    align-content: center;
    padding-top: 1rem;
    padding-left: 1rem;
    color:white;
    max-height: 8rem;
        justify-content: space-between;
        span{
            font-size:2rem; 
        }
    }
}
.title{
    font-size:2rem;
    font-weight:600;
    color: white;
    padding: 1rem 1rem;
    margin: 0;
}
.text{
    font-size: 1.5rem;
    font-weight: 500;
    padding: 1rem;
    color: $colortext;
}
.attribution{
    margin: 0 auto;
}
@media (max-width: 479px) { 
    .container{
        grid-template-columns: 1fr;
        grid-auto-rows: minmax(100px, auto);
    }
    .blue-block{
        grid-column: 1 / 1;
        grid-row: 1 / 1;
    }
    .grey-block{
        grid-column: 1 / 1;
        grid-row: 2 / 2;
    }
    .whaite-block{
        grid-column: 1 / 1;
        grid-row: 3 / 3;
    }
    .whaite-short-block{
        grid-column: 1 / 1;
        grid-row: 4 / 4;
    }
    .grey-long-block{
        grid-column: 1 / 1;
        grid-row: 5 / 5;
    }
}
@media screen and (min-width:480px) and (max-width:800px) {
    .container{
        grid-template-columns: repeat(1, 1fr);
        grid-auto-rows: minmax(100px, auto);
        justify-content: center;
    }
    .blue-block{
        grid-column: 1 / 3;
        grid-row: 1 / 1;
    }
    .grey-block{
        grid-column: 1 / 3;
        grid-row: 2 / 2;
    }
    .whaite-block{
        grid-column: 1 / 3;
        grid-row: 3 / 3;
    }
    .whaite-short-block{
        grid-column: 1 / 3;
        grid-row: 4 / 4;
    }
    .grey-long-block{
        grid-column: 1 / 3;
        grid-row: 5 / 5;
    }
  }
  
